// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

function updateStafftoolsLink() {
  const link =
    document.querySelector('meta[name=site-admin-url]') || document.querySelector('meta[name=site-admin-default-url]')

  if (link instanceof HTMLMetaElement) {
    document.querySelector<HTMLAnchorElement>('.js-site-admin-link')!.href = link.content
  }
}
observe('.js-site-admin-link', updateStafftoolsLink)

function updateSpamuraiUrl() {
  const link = document.querySelector('meta[name=spamurai-url]')

  if (link instanceof HTMLMetaElement) {
    document.querySelector<HTMLAnchorElement>('.js-spamurai-link')!.href = link.content
  }
}
observe('.js-spamurai-link', updateSpamuraiUrl)

function updateServiceCatalog(currentServiceSpan: HTMLMetaElement) {
  const currentServiceUrlSpan = document.querySelector<HTMLMetaElement>('meta[name=current-catalog-service-url]')!

  const knownServiceLink = document.querySelector<HTMLAnchorElement>('.js-catalog-known-service')
  const knownServiceName = document.querySelectorAll<HTMLSpanElement>('.js-catalog-service-name')
  const knownServiceNameField = document.querySelector<HTMLInputElement>('.js-catalog-service-name-field')
  const unknownServiceSpan = document.querySelector<HTMLSpanElement>('.js-catalog-unknown-service')

  const isKnown = currentServiceSpan.content !== 'github/unknown'

  if (unknownServiceSpan) {
    unknownServiceSpan.hidden = isKnown
  }

  if (knownServiceLink) {
    knownServiceLink.hidden = !isKnown
  }

  if (!isKnown) return

  if (knownServiceNameField != null) {
    knownServiceNameField.value = currentServiceSpan.content
  }
  for (const serviceName of knownServiceName) {
    serviceName.textContent = currentServiceSpan.content
  }

  if (knownServiceLink) {
    knownServiceLink.href = currentServiceUrlSpan.content
  }
}

observe('meta[name=current-catalog-service]', {
  initialize: updateServiceCatalog,
  constructor: HTMLMetaElement,
})

function updateBonesUrl(currentServiceSpan: HTMLMetaElement) {
  const isKnown = currentServiceSpan.content !== 'github/unknown'
  const bonesLink = document.querySelector<HTMLAnchorElement>('.js-bones-link')
  if (!bonesLink) return

  if (!isKnown) {
    bonesLink.href = 'https://bones.githubapp.com'
  } else {
    const bonesService = currentServiceSpan.content.replace(/^github\//, '')
    bonesLink.href = `https://bones.githubapp.com/services/${bonesService}`
  }
}

observe('meta[name=current-catalog-service]', {
  initialize: updateBonesUrl,
  constructor: HTMLMetaElement,
})
