// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

on('submit', '.js-feedback-form', submitFeedbackForm)
on('click', '.js-feedback-button', showForm)

function showForm() {
  const formContainerEl = <HTMLElement>document.querySelector('.js-feedback-form-container')
  const responseEl = <HTMLElement>document.querySelector('.js-feedback-form-response-container')
  const errorEl = <HTMLElement>document.querySelector('.js-feedback-form-error')

  if (responseEl && errorEl && formContainerEl) {
    responseEl.hidden = true
    errorEl.hidden = true
    formContainerEl.hidden = false
    const button = formContainerEl.querySelector('.js-feedback-submit')
    if (button instanceof HTMLButtonElement) {
      button.disabled = false
    }
  }
}

function showResponse() {
  const formContainerEl = <HTMLElement>document.querySelector('.js-feedback-form-container')
  const responseEl = <HTMLElement>document.querySelector('.js-feedback-form-response-container')
  const errorEl = <HTMLElement>document.querySelector('.js-feedback-form-error')

  if (responseEl && errorEl && formContainerEl) {
    responseEl.hidden = false
    formContainerEl.hidden = true
    errorEl.hidden = true
  }
}

function showError() {
  const formContainerEl = <HTMLElement>document.querySelector('.js-feedback-form-container')
  const responseEl = <HTMLElement>document.querySelector('.js-feedback-form-response-container')
  const errorEl = <HTMLElement>document.querySelector('.js-feedback-form-error')

  if (responseEl && errorEl && formContainerEl) {
    responseEl.hidden = true
    formContainerEl.hidden = true
    errorEl.hidden = false
  }
}

async function submitFeedbackForm(event: CustomEvent) {
  event.preventDefault()
  const form = event.currentTarget as HTMLFormElement

  const button = form.querySelector('.js-feedback-submit')
  if (button instanceof HTMLButtonElement) {
    button.disabled = true
  }

  const response = await fetch(form.action, {
    method: form.method,
    body: new FormData(form),
    headers: {Accept: 'text/fragment+html'},
  })

  if (response.status === 200) {
    showResponse()
    const link = document.getElementsByClassName('js-feedback-issue-link')[0]
    const issueJson = await response.json()
    if (link instanceof HTMLAnchorElement) {
      link.href = issueJson.url
    }
  } else if (response.status === 422) {
    showError()
    const responseText = await response.text()
    const errorEl = document.getElementsByClassName('js-feedback-form-error')[0]!
    errorEl.innerHTML = responseText
  } else {
    showError()
    const errorEl = document.getElementsByClassName('js-feedback-form-error')[0]!
    errorEl.textContent = 'Sorry, we were unable to create this feedback issue due to a server error.'
  }
}

on('click', '.js-feedback-target-toggle', async function () {
  const feedbackEl = <HTMLElement>document.querySelector('.js-feedback-modal')
  const catalogServiceName = <HTMLElement>feedbackEl.querySelector('.js-catalog-service-name')
  const customTarget = <HTMLElement>feedbackEl.querySelector('.js-feedback-target-container')

  if (catalogServiceName) {
    if (catalogServiceName.hidden) {
      const targetInput = <HTMLInputElement>feedbackEl.querySelector('.js-feedback-target-input')
      targetInput.value = ''
    }
    catalogServiceName.hidden = !catalogServiceName.hidden
  }

  if (customTarget) {
    customTarget.hidden = !customTarget.hidden
  }
})
