function drawRect(rect: DOMRectReadOnly, color: string): HTMLCanvasElement {
  const canvas = document.createElement('canvas')
  canvas.style.width = '100%'
  canvas.style.height = '100%'
  canvas.width = window.innerWidth
  canvas.height = window.innerHeight

  //Position canvas
  canvas.style.position = 'absolute'
  canvas.style.left = '0'
  canvas.style.top = '0'
  canvas.style.zIndex = '100000'
  canvas.style.pointerEvents = 'none'
  document.body.appendChild(canvas)

  const context = canvas.getContext('2d')!
  context.strokeStyle = color
  context.strokeRect(rect.x, rect.y, rect.width, rect.height)
  return canvas
}

// Use a try/catch instead of feature detecting `layout-shift`
// support, since some browsers throw when using the new `type` option.
// https://bugs.webkit.org/show_bug.cgi?id=209216
try {
  const params = new URLSearchParams(document.location.search.substring(1))
  if (params.get('cls')) {
    const po = new PerformanceObserver(list => {
      for (const entry of list.getEntries()) {
        // @ts-expect-error LayoutShift doesn't exist in TS yet.
        if (entry.hadRecentInput) {
          continue
        }
        // @ts-expect-error LayoutShift doesn't exist in TS yet.
        for (const source of entry.sources) {
          // eslint-disable-next-line no-console
          console.log('The following node was shifted unexpectingly')
          // eslint-disable-next-line no-console
          console.log(source.node)
          const canvas = drawRect(source.previousRect, 'red')
          setTimeout(function () {
            canvas.remove()
          }, 2500)
        }
      }
    })

    po.observe({type: 'layout-shift', buffered: true})
  }
} catch (e) {
  // Do nothing if the browser doesn't support this API.
}
