const params = new URLSearchParams(document.location.search.substring(1))
if (params.get('disable-github-bundle')) {
  const githubBundleLinkElement = document.querySelector<HTMLLinkElement>(
    'link[rel=stylesheet][href*="assets/github-"]',
  )
  if (githubBundleLinkElement) {
    githubBundleLinkElement.disabled = true

    document.addEventListener('keypress', ({key, ctrlKey}) => {
      if (ctrlKey && key === 'g') {
        githubBundleLinkElement.disabled = !githubBundleLinkElement.disabled
      }
    })
  }
}
