import {attr, controller, target} from '@github/catalyst'
import {parseHTML} from '@github-ui/parse-html'

@controller
class ShowcaseHealthCheckElement extends HTMLElement {
  @target repoName: HTMLInputElement
  @target result: HTMLElement
  @target loadingIndicator: HTMLElement
  @attr src: string
  @attr errorMessage: string
  @attr csrfToken: string

  async performHealthCheck() {
    this.showLoader()

    if (this.repoName.value === '') {
      this.hideLoader()
      this.result.textContent = ''
      return
    }

    try {
      const response = await this.fetchHealthCheck(this.repoName, this.src, this.csrfToken)

      if (response.ok) {
        const partial = parseHTML(document, await response.text())
        this.hideLoader()
        this.result.textContent = ''
        this.result.append(partial)
      } else {
        this.hideLoader()
        this.result.innerHTML = this.errorMessage
      }
    } catch {
      this.hideLoader()
      this.result.innerHTML = this.errorMessage
    }
  }

  showLoader() {
    this.result.textContent = ''
    this.loadingIndicator.hidden = false
  }

  hideLoader() {
    this.loadingIndicator.hidden = true
  }

  async fetchHealthCheck(repoName: HTMLInputElement, src: string, csrfToken: string): Promise<Response> {
    const body = new FormData()
    body.append('repo_name', repoName.value)

    return await fetch(src, {
      method: 'POST',
      body,
      headers: {
        'Scoped-CSRF-Token': csrfToken,
      },
    })
  }
}
