// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
import {remoteForm} from '@github/remote-form'

// Stats Bar

// Handles toggle bar which also controls if `preview_features?` is enabled
// on the server side.
//
// Makes "`" async of clicking it.
remoteForm('.js-stats-toggle', async function (form, wants) {
  const headerWrapper = document.querySelector('.js-header-wrapper')
  if (headerWrapper) {
    headerWrapper.classList.toggle('stats-ui-enabled')
  }

  const siteAdminStateSpan = document.querySelector('.js-site-admin-mode-state')
  if (siteAdminStateSpan) {
    if (siteAdminStateSpan.innerHTML.trim() === 'off') {
      siteAdminStateSpan.textContent = 'on'
    } else {
      siteAdminStateSpan.textContent = 'off'
    }
  }

  await wants.text()
})

let softNavigationCount = 0
function updateStats(newStats: Element, isPjax = false) {
  const container = document.querySelector<HTMLElement>('#serverstats')
  if (!container) return

  for (const selector of ['.js-stats', '.js-more-stats']) {
    const stale = container.querySelector(selector)
    const fresh = newStats.querySelector(selector)
    if (fresh && selector === '.js-stats') replaceHTMLStatsWithHeaderData(fresh, isPjax)
    if (stale && fresh) {
      stale.replaceWith(fresh)
      if (isPjax) {
        const counter = fresh.querySelector('.js-pjax-count')
        const stat = fresh.querySelector<HTMLElement>('.js-pjax-stat')
        if (!counter || !stat) return
        stat.hidden = false
        counter.textContent = String(softNavigationCount)
      }
    }
  }
}

// Move stats from hidden container on the bottom of the page to the actual
// stats container.
observe('#page-serverstats', el => {
  updateStats(el)

  // Once the stats are updated, the container will be empty, so we don't need it anymore.
  el.remove()
})

document.addEventListener('turbo:before-render', function (event) {
  const contents = (event as CustomEvent).detail?.newBody
  if (!contents) return
  const elem = contents.querySelector('#pjax-stats')
  if (elem) {
    softNavigationCount += 1
    updateStats(elem, true)
  }
})

function updateVoltronStats(staffBarStats: Element) {
  const webServerStats = staffBarStats.querySelector<HTMLElement>('.js-web-server-stats')
  const webServerName = staffBarStats.querySelector<HTMLElement>('.js-server-name')
  const voltronServerTimingMetaTag = document.querySelector<HTMLElement>('meta[name="voltron-timing"]')

  if (!voltronServerTimingMetaTag || !webServerStats || !webServerName) return

  const voltronTiming = Number(voltronServerTimingMetaTag?.getAttribute('value'))
  const timingSpan = staffBarStats.querySelector<HTMLElement>('span')

  if (!timingSpan) return

  const {label, reaction} = classifyPerformance(voltronTiming)
  timingSpan.classList.toggle(Label.Success, Label.Success === label)
  timingSpan.classList.toggle(Label.Warning, Label.Warning === label)
  timingSpan.classList.toggle(Label.Danger, Label.Danger === label)
  webServerStats.textContent = `${formatTime(voltronTiming)}`
  webServerName.textContent = `voltron ${reaction}`
}

observe('#staff-bar-stats', updateVoltronStats)

// formatTime(123.45) => 123ms
// formatTime(1.4567) => 1.5ms
// formatTime(1776.89) => 1.8s
function formatTime(duration: number): string {
  let rounded: number
  if (duration > 999) {
    rounded = +(duration / 1000).toFixed(1)
    return `${rounded}s`
  }
  if (duration < 10) {
    rounded = +duration.toFixed(1)
  } else {
    rounded = Math.round(duration)
  }
  return `${rounded}ms`
}

enum Label {
  Success = 'Label--success',
  Warning = 'Label--warning',
  Danger = 'Label--danger',
}

enum Reaction {
  Hare = '🐇',
  Tortoise = '🐢',
  Snail = '🐌',
}

interface PerformanceClassification {
  label: Label
  reaction: Reaction
}

// Map a duration to the appropriate label and emoji.  Ported from ruby
// Site::Stats#response_time_display.
function classifyPerformance(ms: number): PerformanceClassification {
  if (ms <= 150) return {label: Label.Success, reaction: Reaction.Hare}
  if (ms <= 450) return {label: Label.Warning, reaction: Reaction.Tortoise}
  return {label: Label.Danger, reaction: Reaction.Snail}
}

// The timing data embedded in the staffbar is somewhat less accurate than it
// could be since it is computed before the response is fully done.
// Server-Timing header has better data which is accessible in chrome and
// safari.  This is particularly useful when the response is a 304, so the data
// in the HTML is from an "old" request (the one that was cached) and is
// completely wrong.  This updates the stafftools div with better timing data.
export function replaceHTMLStatsWithHeaderData(perfElement: Element, isPjax = false, globalObj = window) {
  if (!perfElement.parentElement) return
  if (!('performance' in globalObj)) return
  if (perfElement.classList.contains('js-full')) return // cannot handle "full" staffbar yet
  const currentLocation = globalObj.location.toString()
  const stat = isPjax
    ? // if this element was generated for a PJAX request, use those server timings.
      performance
        .getEntriesByType('resource')
        .reverse()
        .find(timing => {
          return timing.initiatorType === 'fetch' && timing.name && timing.name === currentLocation
        })
    : globalObj.performance.getEntriesByType('navigation')[0]
  if (stat && stat.serverTiming) {
    const serverTimings = stat.serverTiming
    const reducer = (accum: Record<string, number>, timing: PerformanceServerTiming) => {
      accum[timing.name] = timing.duration
      return accum
    }
    const serverTimingsByName = serverTimings.reduce(reducer, {})
    const voltronServerTimingMetaTag = document.querySelector<HTMLElement>('meta[name="voltron-timing"]')
    const webServer = voltronServerTimingMetaTag
      ? Number(voltronServerTimingMetaTag?.getAttribute('value'))
      : serverTimingsByName['Unicorn']
    if (!webServer) return
    const webServerName = voltronServerTimingMetaTag ? 'voltron' : 'unicorn'
    const timingSpan = perfElement.querySelector('span')
    if (timingSpan) {
      const {label, reaction} = classifyPerformance(webServer)
      const glb = serverTimingsByName['GLB'] || 0
      const nginx = serverTimingsByName['Nginx'] || 0
      const internet =
        'responseStart' in stat && 'requestStart' in stat
          ? stat.responseStart - stat.requestStart - webServer - glb - nginx
          : 0
      const wasCached = serverTimingsByName['Render'] === 0
      const webServerFormatted = `${isPjax ? '⚡️ ' : ''}${formatTime(webServer)}${
        wasCached ? ' (cached) ' : ' '
      }${reaction}`
      const tooltip = `${formatTime(glb)} glb → ${formatTime(
        nginx,
      )} nginx → ${webServerFormatted} ${webServerName} → ${formatTime(internet)} internet`
      timingSpan.textContent = webServerFormatted
      timingSpan.setAttribute('aria-label', tooltip)
      timingSpan.classList.toggle(Label.Success, Label.Success === label)
      timingSpan.classList.toggle(Label.Warning, Label.Warning === label)
      timingSpan.classList.toggle(Label.Danger, Label.Danger === label)
    }
  }
}

document.addEventListener('staffbar-update', e => {
  if (!(e instanceof CustomEvent)) return

  const duration = e.detail.duration
  const staffbarTiming = document.querySelector('#dialog-show-staffbar-stats-response-time')

  if (!staffbarTiming) return

  const {label, reaction} = classifyPerformance(duration)

  const formattedTime = `${formatTime(duration)} ${reaction}`

  staffbarTiming.textContent = formattedTime
  staffbarTiming.classList.toggle(Label.Success, Label.Success === label)
  staffbarTiming.classList.toggle(Label.Warning, Label.Warning === label)
  staffbarTiming.classList.toggle(Label.Danger, Label.Danger === label)
  staffbarTiming.removeAttribute('data-show-dialog-id')
})
