// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {parseHTML} from '@github-ui/parse-html'
import {ready} from '@github-ui/document-ready'

function hidePerformancePane(paneSelector: string) {
  const pane = document.querySelector(paneSelector)
  if (pane instanceof HTMLElement) {
    pane.hidden = true
  }
}

function togglePerformancePane(paneSelector: string) {
  const stats = document.querySelector('#serverstats')
  const pane = document.querySelector(paneSelector)
  if (pane instanceof HTMLElement && stats) {
    pane.hidden = !pane.hidden
    stats.append(pane)
  }
}

function toggleSQLPerformancePane() {
  hidePerformancePane('#graphql_performance_pane')
  togglePerformancePane('#mysql_performance_pane')
}

function toggleGraphQLPerformancePane() {
  hidePerformancePane('#mysql_performance_pane')
  togglePerformancePane('#graphql_performance_pane')
}

;(async () => {
  await ready
  if (location.search.match(/graphql_query_trace=true/)) {
    toggleGraphQLPerformancePane()
    // Apply previous filters and open the detailed profile if there is one
    applyGraphQLTraceMsCutoff()
    applyGraphQLFocusPath()
    const dialog = document.querySelector('.js-graphql-trace-profile-dialog')
    if (dialog instanceof HTMLElement) {
      dialog.click()
    }
  } else if (location.search.match(/mysql_query_trace=true/)) {
    toggleSQLPerformancePane()
  }
})()

on('click', '.js-graphql-performance-pane-toggle', event => {
  toggleGraphQLPerformancePane()
  event.preventDefault()
  event.stopPropagation()
})

on('click', '.js-graphql-toggle-children', function (event) {
  // Expand the children of this node
  event.currentTarget.closest<HTMLElement>('.js-graphql-query-trace-node')!.classList.toggle('children-expanded')
})

// After the user enters a millisecond value, re-apply filters to hide nodes
// that were faster than that value. Replace any hidden nodes
// with a link to show the hidden nodes.
function applyGraphQLTraceMsCutoff() {
  // Read the user-provided value and quit if it's actually empty
  const cutoffInput = document.querySelector<HTMLInputElement>('.js-graphql-query-trace-cutoff-ms')!
  const cutoffMs = Number(cutoffInput.value)
  if (!cutoffMs) {
    return
  }

  // Find each container, which we'll filter individually,
  // and insert a "Show x hidden" link if neccessary
  const traceNodeChildren = document.querySelectorAll('.js-graphql-stats-node-children')
  for (const el of traceNodeChildren) {
    let hiddenNodesCount = 0
    for (const childEl of el.children) {
      if (childEl.classList.contains('js-graphql-query-trace-hidden-count')) {
        // We're going to replace this "Show x hidden" link, so remove it for now
        el.removeChild(childEl)
      } else if (childEl instanceof HTMLElement) {
        // It's a node
        const nodeMsString = childEl.getAttribute('data-graphql-node-ms')
        const nodeMs = Number(nodeMsString)
        if (nodeMs != null && nodeMs > cutoffMs) {
          childEl.hidden = false
        } else {
          hiddenNodesCount += 1
          childEl.hidden = true
        }
      }
    }
    // If we hid any nodes in this container, add a link to show those nodes
    if (hiddenNodesCount > 0) {
      const link = parseHTML(
        document,
        // eslint-disable-next-line github/unescaped-html-literal
        `<a role="button" class="js-graphql-query-trace-hidden-count">${hiddenNodesCount} more hidden</span>`,
      )
      el.append(link)
    }
  }
}

// Based on a user-provided value, expand the accordion to show a certain field in the query
function applyGraphQLFocusPath() {
  // Get the user-provided value, return if it's actually empty
  const focusPath = document.querySelector<HTMLInputElement>('.js-graphql-query-trace-focus-path')!.value
  if (!focusPath) {
    return
  }
  // Check all the nodes, we're going to:
  // - find the user-specified one
  // - expand it and its parents
  const traceNodes = document.querySelectorAll('.js-graphql-query-trace-node')
  for (const el of traceNodes) {
    el.classList.remove('children-expanded')
    const nodePath = el.getAttribute('data-graphql-node-path')
    const nodeName = el.getAttribute('data-graphql-node-name')
    if (nodePath === focusPath || nodeName === focusPath) {
      // It's a match, so expand this field
      // and find all of its parents, and expand them too
      let expandedNode: Element | null = el
      while (expandedNode) {
        expandedNode.classList.add('children-expanded')
        // Look for the next one up the tree:
        if (expandedNode.parentElement) {
          try {
            expandedNode = expandedNode.parentElement.closest<HTMLElement>('.js-graphql-query-trace-node')!
          } catch (err) {
            // We reached the top of the tree, no more parents
            expandedNode = null
          }
        }
      }
    }
  }
}

on('click', '.js-graphql-query-trace-filter-ms', applyGraphQLTraceMsCutoff)
on('click', '.js-graphql-query-trace-expand-path', applyGraphQLFocusPath)

on('click', '.js-graphql-query-trace-hidden-count', function (event) {
  // When you click on "Show x hidden", remove the link and show the hidden nodes
  const showHiddenButton = event.currentTarget
  const container = showHiddenButton.parentElement
  if (container) {
    container.removeChild(showHiddenButton)
    for (const child of container.children) {
      if (child instanceof HTMLElement) child.hidden = false
    }
  }
})

function sortBy<T extends Element>(elements: HTMLCollectionOf<T>, sortKeyFunction: (el: T) => number): T[] {
  return [...elements].sort(function (elA, elB) {
    const a = sortKeyFunction(elA)
    const b = sortKeyFunction(elB)
    return b - a
  })
}

// Create a function to return the number of a sort-value of an element, by the given index.
function cellAtIndex(index: number) {
  return function (row: Element) {
    const cell = row.querySelectorAll('td')[index]!
    const value = cell.getAttribute('data-sort-value') || cell.textContent
    return value == null ? NaN : parseFloat(value)
  }
}

// Compose a given function to return the reverse of that functions return
// value.
function reversed<T>(f: (x: T) => number): (x: T) => number {
  return function (x: T) {
    return -f(x)
  }
}

function sortRowsBy(tbody: Element, func: (el: Element) => number) {
  for (const row of sortBy(tbody.getElementsByTagName('tr'), func)) {
    tbody.append(row)
  }
}

type TemplateSQLTraceHighlight = {comment: Comment; queries: [string]}

function extractQueryStatement(queryElement: Element): string | undefined {
  const statementElement = queryElement.getElementsByTagName('code')
  const text = statementElement[0]?.textContent?.trim()
  const queryWithoutComment = text?.replace(/\/\*.*\*\//, '')

  return queryWithoutComment
}

function extractStackTrace(queryElement: Element) {
  return Array.from(queryElement.getElementsByClassName('query-backtrace-link'))
}

function findViewTemplateOnStacktrace(stackTrace: Element[]) {
  return stackTrace.find(stackFrame => {
    const reference = stackFrame.getAttribute('href')?.trim()
    if (reference) {
      return includesTemplateOrComponent(reference)
    }
  })
}

function fileNameFromStackFrame(stackFrame: Element) {
  return stackFrame.getAttribute('href')?.trim().split('/').slice(-1)[0]!.split('#')[0]
}

function enrichTemplateAnnotations(templateAnnotations: Map<string, Comment>) {
  const queries = Array.from(document.getElementsByClassName('sql'))

  return queries.reduce((acc, queryElement: Element) => {
    const backtraces = extractStackTrace(queryElement)
    const viewTemplateStackFrame = findViewTemplateOnStacktrace(backtraces)

    if (viewTemplateStackFrame) {
      const fileName = fileNameFromStackFrame(viewTemplateStackFrame)
      if (fileName) {
        const comment = templateAnnotations.get(fileName)
        if (comment !== undefined) {
          const queryStatement = extractQueryStatement(queryElement)
          if (queryStatement) {
            const existingNode = acc.get(fileName)
            if (existingNode) {
              existingNode.queries.push(queryStatement)
            } else {
              acc.set(fileName, {comment, queries: [queryStatement]})
            }
          }
        }
      }
    }

    return acc
  }, new Map<string, TemplateSQLTraceHighlight>())
}

function getAllTemplateAnnotations(rootElem: Element) {
  const comments = new Map<string, Comment>()
  const iterator = document.createNodeIterator(rootElem, NodeFilter.SHOW_COMMENT, {
    acceptNode() {
      return NodeFilter.FILTER_ACCEPT
    },
  })

  let curNode

  while ((curNode = iterator.nextNode())) {
    const text = curNode?.nodeValue?.trim()
    if (!text) {
      continue
    }

    if (isTemplateAnnotation(text)) {
      const templateName = text.split('/').slice(-1)[0]!
      comments.set(templateName, <Comment>curNode)
    }
  }

  return comments
}

function isTemplateAnnotation(str: string) {
  return str.startsWith('BEGIN') && includesTemplateOrComponent(str)
}

function includesTemplateOrComponent(str: string) {
  return str.includes('app/views') || str.includes('app/components')
}

function wrapComments(templateAnnotations: Map<string, TemplateSQLTraceHighlight>) {
  for (const [templateName, templateSqlTrace] of templateAnnotations) {
    if (templateSqlTrace.comment !== undefined) {
      const nextElement = templateSqlTrace.comment.nextElementSibling

      if (nextElement !== null) {
        if (nextElement.classList.contains('template-highlight-frame')) {
          addQueriesToExistingTemplateAnnotationWrapper(nextElement, templateName, templateSqlTrace.queries)
        } else {
          const wrapperDiv = createTemplateAnnotationWrapper(templateName, templateSqlTrace.queries)
          wrapElement(nextElement, wrapperDiv)
        }
      }
    }
  }
}

function addQueriesToExistingTemplateAnnotationWrapper(
  existingWrapper: Element,
  templateName: string,
  queriesRanByTemplate: string[],
) {
  let dataAttribute = existingWrapper.getAttribute('data-traced-queries')
  if (dataAttribute && dataAttribute.length > 0) {
    dataAttribute += '\n\n'
    dataAttribute += `${templateName} \n\n`
    dataAttribute += queriesRanByTemplate.join('\n')
    existingWrapper.setAttribute('data-traced-queries', dataAttribute)
  }
}

function createTemplateAnnotationWrapper(templateName: string, queriesRanByTemplate: string[]) {
  let dataAttribute = `${templateName} \n\n`
  dataAttribute += queriesRanByTemplate.join('\n')
  const frameDiv = document.createElement('div')
  frameDiv.className = `template-highlight-frame ${templateName} rounded-2`
  frameDiv.setAttribute('data-traced-queries', dataAttribute)

  return frameDiv
}

function wrapElement(el: Element, wrapper: Node) {
  el.parentNode?.insertBefore(wrapper, el)
  wrapper.appendChild(el)
}

function showTemplateWrappers() {
  const wrappers = Array.from(document.getElementsByClassName('template-highlight-frame'))
  if (wrappers.length > 0) {
    for (const wrapper of wrappers) {
      wrapper.classList.remove('hidden-template-wrapper')
    }
  } else {
    const comments = getAllTemplateAnnotations(document.body)
    const templateAnnotations = enrichTemplateAnnotations(comments)
    wrapComments(templateAnnotations)
  }
}

function hideTemplateWrappers() {
  const wrappers = Array.from(document.getElementsByClassName('template-highlight-frame'))
  for (const wrapper of wrappers) {
    wrapper.classList.add('hidden-template-wrapper')
  }
}

on('change', '#highlight-templates-that-ran-queries', function () {
  const input = <HTMLInputElement>document.getElementById('highlight-templates-that-ran-queries')

  if (input.checked) {
    showTemplateWrappers()
  } else {
    hideTemplateWrappers()
  }
})

on('click', '.js-toggle-mysql-tracer', function () {
  toggleSQLPerformancePane()

  const url = new URL(window.location.href, window.location.origin)

  // Update our version of the URL to flip the param to false, which will keep the pane hidden on page reload
  url.searchParams.delete('mysql_query_trace')

  // Remove the xhr_stats query param as well to clean up the URL
  url.searchParams.delete('xhr_stats')

  // Push the updated URL to browser's history
  history.pushState({}, '', url)
})

observe('.template-highlight-frame', function (container) {
  const hoverClass = 'template-highlight-frame-hover'
  container.addEventListener('mouseover', function (e) {
    container.classList.add(hoverClass)
    e.stopPropagation()
  })

  container.addEventListener('mouseout', function (e) {
    container.classList.remove(hoverClass)
    e.stopPropagation()
  })
})

on('click', '.cluster-dropdown-item', function (event) {
  const data = event.currentTarget.getAttribute('data')
  const table = event.currentTarget.closest<HTMLElement>('table')!
  const tbody = table.querySelector<HTMLElement>('tbody')!

  const menu = event.currentTarget.closest<HTMLElement>('details')!
  if (!menu) return
  menu.removeAttribute('open')

  const th = event.currentTarget.closest<HTMLElement>('th')!
  const parent = th.parentElement
  if (!parent) return
  const index = Array.prototype.indexOf.call(parent.children, th)

  for (const row of tbody.getElementsByTagName('tr')) {
    const cell = row.querySelectorAll('td')[index]!
    const value = cell.getAttribute('data')
    row.hidden = data !== '*' && value !== data
  }
})

on('click', '.js-sortable-table th', function (event) {
  if (event.currentTarget.classList.contains('js-not-sortable')) {
    return
  }

  const table = event.currentTarget.closest<HTMLElement>('table')!
  const thead = table.querySelector<HTMLElement>('thead')!
  const tbody = table.querySelector<HTMLElement>('tbody')!
  for (const th of thead.getElementsByTagName('th')) {
    th.classList.remove('is-sort-asc', 'is-sort-desc')
  }
  const parent = event.currentTarget.parentElement
  if (!parent) return
  const index = Array.prototype.indexOf.call(parent.children, event.currentTarget)
  const lastIndex = table.getAttribute('data-last-sort-index')
  const currentOrder = event.currentTarget.getAttribute('data-sort-order')
  const newOrder = `${index}` === lastIndex && currentOrder === 'asc' ? 'desc' : 'asc'
  let sorter = cellAtIndex(index)
  if (newOrder === 'asc') {
    sorter = reversed(sorter)
  }
  table.setAttribute('data-last-sort-index', index.toString())
  event.currentTarget.setAttribute('data-sort-order', newOrder)
  event.currentTarget.classList.add(`is-sort-${newOrder}`)
  sortRowsBy(tbody, sorter)
  event.preventDefault()
  event.stopPropagation()
})

on('click', '.js-query-table-selector', function (event) {
  const container = event.currentTarget.closest<HTMLElement>('.js-mysql-performance-pane')!
  for (const selector of container.getElementsByClassName('js-query-table-selector')) {
    selector.classList.remove('selected')
  }
  event.currentTarget.classList.add('selected')
  for (const table of container.getElementsByClassName('js-query-table')) {
    if (table instanceof HTMLElement) table.hidden = true
  }
  const tableID = event.currentTarget.getAttribute('data-query-table-id')!
  const table = document.getElementById(tableID)
  if (!table) {
    return
  }
  table.hidden = false
  event.preventDefault()
  event.stopPropagation()
})

on('click', '.js-query-search', function (event) {
  const row = event.currentTarget.closest<HTMLElement>('.js-query-row')!
  const digest = row.getAttribute('data-query-digest')
  const table = event.currentTarget.closest<HTMLElement>('.js-query-table')!
  for (const otherRow of table.getElementsByClassName('js-query-row')) {
    if (digest === otherRow.getAttribute('data-query-digest')) {
      otherRow.classList.add('highlighted-row')
      otherRow.classList.remove('dimmed-row')
    } else {
      otherRow.classList.remove('highlighted-row')
      otherRow.classList.add('dimmed-row')
    }
  }
  event.preventDefault()
  event.stopPropagation()
})

on('click', '.js-query-clear-search', function (event) {
  const table = event.currentTarget.closest<HTMLElement>('.js-query-table')!
  for (const row of table.getElementsByClassName('js-query-row')) {
    row.classList.remove('highlighted-row', 'dimmed-row')
  }
  event.preventDefault()
  event.stopPropagation()
})
