import {dialog} from '@github-ui/details-dialog'
import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'
import {loaded} from '@github-ui/document-ready'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import sudo from '@github-ui/sudo'

// Hotkey: \
//
// prompt for sudo, then show stafftools modal

async function showStafftoolsModal(url: string) {
  redirectToAdmin(true)
  const sudoPassed = await sudo()
  if (sudoPassed) await dialog({content: fetchSafeDocumentFragment(document, url)})
}

// Returns an string if the user is not required to use an admin host or
// they are already on an pseudo-admin host (non-prod, labs).
// Returns a full URL for users required to use admin hosts in production.
function adminHostname(): string {
  const parsingElement = document.createElement('a')
  parsingElement.href =
    document.head?.querySelector<HTMLMetaElement>('meta[name="site-admin-default-url"]')?.content || ''
  return parsingElement.hostname
}

function redirectToAdmin(popModalAfterDirect: boolean) {
  const adminHost = adminHostname()
  if (window.location.hostname !== adminHost) {
    const redirectParam = `_admin_modal=${popModalAfterDirect.toString()}`
    const search = (window.location.search ? `${window.location.search}&` : '?') + redirectParam
    window.location.href = ['https://', adminHost, window.location.pathname, search].join('')
  }
}

on('click', '.js-stafftools-toggle-modal', async function ({currentTarget}) {
  const url = currentTarget.getAttribute('data-modal-url') || ''
  try {
    await showStafftoolsModal(url)
  } catch (error) {
    window.location.href = url
  }
})

on('click', '.js-site-admin-link', () => {
  redirectToAdmin(false)
})
;(async () => {
  await loaded
  if (window.location.search.match(/_admin_modal=true/)) {
    const toggleModal = document.querySelector('.js-stafftools-toggle-modal')
    if (!toggleModal) return
    const url = toggleModal.getAttribute('data-modal-url') || ''
    showStafftoolsModal(url)
  }
})()
