import {dialog} from '@github-ui/details-dialog'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

observe('#line-profile', {
  constructor: HTMLTemplateElement,
  add(el) {
    const content = el.content.cloneNode(true)
    if (!(content instanceof DocumentFragment)) return
    dialog({
      content,
      dialogClass: 'width-full container-xl Box-body overflow-auto',
    })
    el.remove()
  },
})
