// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

const violationStates = ['violations-pending', 'violations-loading', 'has-violations', 'has-no-violations']

function checkTradeControlsViolations(violationsContainer: Element): Promise<Response> {
  const userLogin = violationsContainer.getAttribute('data-user-login')
  return fetch(`/stafftools/users/${userLogin}/trade_compliance/check_violations`, {
    headers: {
      'X-Requested-With': 'XMLHttpRequest',
    },
  })
}

function setViolationState(container: Element, state: string) {
  container.classList.remove(...violationStates)
  if (violationStates.includes(state)) {
    container.classList.add(state)
  }
}

async function fetchViolations(container: Element) {
  const listContainer = document.querySelector<HTMLElement>('.js-violations-list')!
  listContainer.textContent = ''
  setViolationState(container, 'violations-loading')

  const response = await checkTradeControlsViolations(container)
  const jsonBody = await response.json()
  const violations = jsonBody.violations

  if (violations.length === 0) {
    setViolationState(container, 'has-no-violations')
  } else {
    setViolationState(container, 'has-violations')
    for (const violation of violations) {
      const item = document.createElement('li')
      item.appendChild(document.createTextNode(violation))
      listContainer.appendChild(item)
    }
  }
}

// Handle violations check clicks
on('click', '#js-check-trade-controls-violations', function (event) {
  const violationsContainer = event.currentTarget.closest<HTMLElement>('.js-trade-controls-violations-container')!
  fetchViolations(violationsContainer)
})
