// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

observe('.js-project-container .js-more-stats', function (el) {
  const container = el.closest('.js-project-column, .js-project-triage-pane, .js-project-activity-pane')
  if (container) {
    const statsContainer = container.querySelector('.js-project-stats')
    if (statsContainer) {
      statsContainer.textContent = ''
      statsContainer.appendChild(el)
    } else {
      el.remove()
    }
  }
})

observe('.js-project-container .js-stats', function (el) {
  // Remove regular stats since they are unused
  el.remove()
})

observe('#serverstats.more-enabled', {
  add() {
    for (const projectStats of document.querySelectorAll('.js-project-stats-container')) {
      /* eslint-disable-next-line github/no-d-none */
      projectStats.classList.remove('d-none')
    }
  },
  remove() {
    for (const projectStats of document.querySelectorAll('.js-project-stats-container')) {
      /* eslint-disable-next-line github/no-d-none */
      projectStats.classList.add('d-none')
    }
  },
})
