// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

function warn(el: Element) {
  // eslint-disable-next-line no-console
  console.log('Deprecated analytics events must be migrated to github/hydro-schemas', el)
}

observe('[data-field]', warn)
observe('[data-ga-load]', warn)
observe('[data-ga-event]', warn)
observe('[data-ga-event-action]', warn)
observe('[data-ga-event-category]', warn)
observe('[data-ga-event-label]', warn)
observe('.js-signup-source', warn)
observe('.js-enterprise-trial-ga-id', warn)

on('click', '[data-ga-click]', function (event) {
  warn(event.currentTarget)
})

on('change', '[data-ga-change]', function (event) {
  warn(event.currentTarget)
})
