import {controller, target} from '@github/catalyst'
import {getCookie, setCookie} from '@github-ui/cookies'

const isAccessibilityScanningEnabled = () => getCookie('accessibilityScan')?.value !== 'false'
const maxDisableDays = 3

@controller
class AccessibilityScanToggleElement extends HTMLElement {
  @target button: HTMLElement

  connectedCallback() {
    this.button.setAttribute('aria-pressed', `${isAccessibilityScanningEnabled()}`)
    // eslint-disable-next-line custom-elements/no-dom-traversal-in-connectedcallback
    const tooltip = this.ownerDocument.querySelector(`tool-tip[for="${this.button.id}"]`)
    if (tooltip) {
      tooltip.textContent = this.#tooltipText(isAccessibilityScanningEnabled())
    }
    isAccessibilityScanningEnabled() && this.button.classList.add('active')
  }

  toggleAccessibilityScan() {
    const expireTime = new Date(new Date().getTime() + maxDisableDays * 24 * 60 * 60 * 1000) // 3 days
    setCookie('accessibilityScan', `${!isAccessibilityScanningEnabled()}`, expireTime.toUTCString())
    !isAccessibilityScanningEnabled() &&
      // eslint-disable-next-line i18n-text/no-en
      setCookie('flash-notice', encodeURIComponent('The accessibility axe scan has been disabled.'))
    this.button.classList.toggle('active')
    this.button.setAttribute('aria-pressed', `${isAccessibilityScanningEnabled()}`)

    window.location.reload()
  }

  #tooltipText(accessibilityScanEnabled: boolean) {
    if (accessibilityScanEnabled) {
      // eslint-disable-next-line i18n-text/no-en
      return `Disable accessibility axe scan (up to ${maxDisableDays} days)`
    } else {
      // eslint-disable-next-line i18n-text/no-en
      return `Enable accessibility axe scan`
    }
  }
}
